@media only screen and (max-width: 2560px) {
  .hiddeNavMenu {
    display: block;
  }
}

@media only screen and (max-width: 1440px) {
  .skill-box {
    width: 900px;
    height: 600px;
  }

  .my-work-box {
    width: 1000px;
  }
}


@media only screen and (max-width: 1024px) {
  .hiddeNavMenu {
    display: none;
  }

  .nav-icon {
    display: block;
    cursor: pointer;
  }

  .container {
    display: block;
  }

  .container-1 {
    text-align: center;
    margin-top: -10px;
    margin-right: 100px;
    margin-left: 100px;
  }

  .skill-box {
    height: 550px;
    margin: auto;
  }

  .sidebar {
    width: 50px;
    height: 50px;
  }

  .cybr-ul {
    width: 250px;
    height: 400px;
    z-index: 5;
    position: relative;
  }

  .my-work-box {
    width: 900px;
    margin: auto;
    height: 600px;
  }

  .projects-title {
    display: block;
  }
}


@media only screen and (max-width: 768px) {
  .skill-box {
    width: 600px;
    height: 1000px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
   
  .item-1 {
    width: 90%;
  }

  .my-work-box {
    width: 700px;
    margin: auto;
    height: 650px;
  }
}

@media only screen and (max-width: 425px) {

  .container-1 {
    margin-top: 0px;
    margin-right: 0px;
    margin-left: 0px;
  }

  .skill-box {
    width: 400px;
    height: 1000px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .my-work-box {
    width: 410px;
    height: 400px;
  }

}

@media only screen and (max-width: 375px) {

  .container-1 {
    width: 410px;
    margin-top: 0px;
    margin-right: 0px;
    margin-left: 0px;
  }

  .container-2 {
    width: 410px;
  }

  .skill-box {
    width: 350px;
    height: 1000px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .work-container {
    width: 410px;
  }

  .my-work-box {
    width: 390px;
    height: 400px;
  }

}